/* Container for the entire banner image */
.image-container {
    position: relative;
    width: 100%;
    height: 400px; /* Default height for large screens */
    overflow: hidden;
  }
  
  .image-container img {
    width: 100%;
    height: 100%;
    object-fit: cover; /* Ensures the image covers the area without distortion */
    filter: brightness(70%); /* Adds a slight dim effect for text visibility */
  }
  
  /* Overlay for text and other elements */
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    background: rgba(0, 0, 0, 0.4); /* Semi-transparent overlay */
    color: #fff; /* Text color */
    padding: 1rem;
  }
  
  .overlay h1 {
    font-size: 3rem; /* Large heading */
    font-weight: bold;
    margin: 0;
    text-transform: uppercase;
  }
  
  
.overlay p {
  color: white;
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
  opacity: 0; /* Initially hidden */
  animation: fadeInOut 30s infinite; /* Trigger animation */
}

@keyframes fadeInOut {
  0% {
      opacity: 0;
  }
  10% {
      opacity: 1;
  }
  90% {
      opacity: 1;
  }
  100% {
      opacity: 0;
  }
}
  
  /* Responsive Design */
  @media (max-width: 1200px) {
    .image-container {
      height: 350px;
    }
  
    .overlay h1 {
      font-size: 2.5rem;
    }
  
    .overlay p {
      font-size: 1rem;
    }
  }
  
  @media (max-width: 768px) {
    .image-container {
      height: 300px;
    }
  
    .overlay h1 {
      font-size: 2rem;
    }
  
    .overlay p {
      font-size: 0.9rem;
    }
  }
  
  @media (max-width: 576px) {
    .image-container {
      height: 250px;
    }
  
    .overlay h1 {
      font-size: 1.5rem;
    }
  
    .overlay p {
      font-size: 0.8rem;
    }
  }
  